<template>
  <div class="container vcenter">
    <div id="content" class="u-full-width">
      <div>
              <h5 class="text-error" v-show="currentStep!==5">正宗大相家粉条预约</h5>
              <div class="row" v-show="currentStep===1">
                <div class="columns xs-twelve">
                  <p>我们承诺所有商品均为2016年12月份出品，均为精选正宗大相家粉条。您可以通过公众号（ifentiao）或<a href="">点击这里</a>了解更多情况。</p>
                  <p>我们为胶州市区的朋友提供当天送货上门服务。青岛其他市区的朋友需要根据您的下单时间与接货地点统一协调。</p>
                  <p>有疑问请<a href="tel://18669783161">致电我们</a></p>
                </div>
                <button class="button button-primary u-pull-right" @click="onNextStep">点击预约</button>
              </div>
         </div> 
              <!-- STEP PRODUCTS START -->
              <div v-show="currentStep===2">
                <div class="row">
                    <div class="columns xs-twelve">
                      <label class="block">第1步：请选择下面的商品</label>
                          <input type="checkbox" id="packet" value="packet" @click="productChecked">
                            <label for="packet">礼盒(450克×4包)</label>
                           <input type="checkbox" id="bulk" value="bulk" @click="productChecked">
                          <label for="bulk" >散装(5斤装)</label>
                    </div>
                </div>
                <div class="row item" v-show="checkbox.packet.visible">
                      <div class="columns xs-five">
                      <span class="text-error">精品礼盒</span>
                      </div>
                      <div class="columns xs-seven">
                        <number :price="70" v-model="checkbox.packet.quantity"></number>
                      </div>
                </div>
                <div class="row item" v-show="checkbox.bulk.visible">
                      <div class="columns xs-five">
                      <span class="text-error">实惠散装</span>
                      </div>
                      <div class="columns xs-seven">
                        <number :price="75" v-model="checkbox.bulk.quantity"></number>
                      </div>
                </div>
              </div>

              <!-- STEP PRODUCTS END -->

              <!-- STEP ADDRESS START -->
              <div  v-show="currentStep===3">
                  <label for="address">第2步：约定接货地点</label>
                  <select name="address" class="u-full-width" style="margin-top:15px" @change="areaSelected" v-model="area">
                    <option v-for="(option, index) in areas" :value="index" :disabled="index===0">{{option}}</option>
                  </select>
                  <div v-show="showAddressInput">
                    <label>您在{{areas[area]}}的位置</label>
                    <input type="text" class="u-full-width" placeholder="建议填写您附近的地标" v-model="address">
                  </div>
              </div>
              <!-- STEP ADDRESS END -->

              <!-- STEP MOBILE START -->
              <div v-show="currentStep===4">
                <label for="phone">第3步：填写并验证手机 </label>
                <div class="row">
                  <div class="columns twelve">
                    <input class="u-full-width" type="number" maxlength="11" placeholder="您的手机号码" v-model="mobile">
                  </div>
                </div>
                <div class="row">
                  <div class="columns xs-six">
                    <input class="u-full-width" type="number" placeholder="您收到的短信码" id="authcode" v-model="authcode">
                  </div>
                  <div class="columns xs-six">
                    <button class="button button-primary" :disabled="!isMobile" @click.prevent="getSms">获取验证码</button>
                  </div>
                </div>
              </div>
              <!-- STEP MOBILE END -->

              <!-- STEP SUBMIT START -->
              <div v-show="currentStep===5">
                  <h5>信息核对</h5>
                <ul class="card">
                <li v-for="item in products">{{item.name}} × {{item.quantity}}</li>
                <li>您的手机：{{mobile}}</li>
                <li>送货地址：{{theAddress}}</li>
                </ul>
                <div class="row">
                  <div class="columns xs-six">
                    <a href="#" class="button button-success" @click="goto(2)">修改一下</a>
                  </div>
                  <div class="columns xs-six">
                    <button class="button button-primary" @click.prevent="submit">提交预订</button>
                  </div>
                </div>
              </div>
              <!-- STEP SUBMIT END -->

              <div class="row" style="margin-top:25px;">
                <a href="#" class="button button-primary u-pull-right" style="margin-left: 10px" @click="onNextStep" v-show="nextEnabled">下一步</a>
              </div>
              <p style="text-indent: 0" class="text-silence" v-show="currentStep>1">注：请认真填选相关信息，送货前我们会再次与您核实地址与数量。有疑问请<a href="tel://18669783161">点击通话</a></p>
      <!-- <div class="row">
        预订商品：<ul><li v-for="item in result.products">{{item.name}} <span>{{item.quantity}}</span></li></ul>
      </div> -->
    </div>
  </div>
  </template>

  <script>
  import Number from './components/numberInput.vue'
  import {minlength, isMobile} from './validator'
  import {AREA, API_ROOT} from './config'
  export default {
    data () {
      return {
        currentStep: 1,
        areas: AREA,
        showAddressInput: false,
        area: 0,
        address: '',
        checkbox: {
          bulk: {},
          packet: {}
        },
        products: {},
        mobile: '',
        authcode: ''
      }
    },
    methods: {
      productChecked (event) {
        var obj = event.target.id
        var visible = this.checkbox[obj].visible
        this.checkbox[obj] = {name: event.target.nextElementSibling.textContent, visible: !visible, quantity: 1}
        this.products = window._.reject(this.checkbox, function (item) {
          return !item.visible
        })
      },
      onNextStep () {
        this.currentStep += 1
      },
      areaSelected () {
        this.showAddressInput = true
      },
      getSms (event) {
        this.$http.post(API_ROOT + 'getsms', {mobile: this.mobile}).then((response) => {
          window.alert('验证码已发，请查收')
        }, (response) => {
          window.alert(response.body)
          window.location.reload()
        })
      },
      submit () {
        this.$http.post(API_ROOT + 'submit', {mobile: this.mobile, authcode: this.authcode, detail: this.productsForSubmit, address: this.theAddress, device: this.device}).then((response) => {
          window.alert(response.body)
          window.location.reload()
        }, (response) => {
          window.alert(response.body)
          // window.location.reload()
        })
      },
      goto (page) {
        this.currentStep = page
      }
    },
    computed: {
      nextEnabled () {
        return this.stepCheck['step' + this.currentStep]
      },
      prevEnabled () {
        return this.currentStep > 1
      },
      stepCheck () {
        return {
          step1: false, // 前言
          step2: this.checkbox.bulk.visible || this.checkbox.packet.visible,
          step3: minlength(this.address, 2),
          step4: isMobile && this.authcode !== '',
          step5: false
        }
      },
      productsForSubmit () {
        var data = window._.map(this.products, function (item) {
          return item.name + '：' + item.quantity + '件'
        })
        return window._.join(data, '&')
      },
      theAddress () {
        return this.areas[this.area] + this.address
      },
      isMobile () {
        return isMobile(this.mobile)
      },
      device () {
        return navigator.userAgent
      }
    },
    components: {
      Number
    }
  }
  </script>

  <style>
    #content {
    /*position: relative;*/
    margin: 50px auto;
    min-height: 200px;
    z-index: 100;
    padding: 30px;
    border: 1px solid #383838;
    background: rgba(242, 242, 242,0.9);
    -webkit-border-radius: 8px;
    -moz-border-radius: 8px;
    border-radius: 8px;
    -webkit-box-shadow: 0px 1px 6px #3F3F3F;
    -moz-box-shadow: 0px 1px 6px #3F3F3F;
    box-shadow: 0px 1px 6px #3F3F3F;
}
/*#content:after {
    background: #F9F9F9;
    margin: 10px;
    position: absolute;
    content: " ";
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    z-index: -1;
    border: 1px #E5E5E5 solid;
    -webkit-border-radius: 8px;
    -moz-border-radius: 8px;
    border-radius: 8px;
}*/
h5 {
  text-align: center;
  border-bottom: 2px solid #8c6622;
}
  </style>
}



