<template>
  <div class="quantity">
  <input type="number" ref="input" :min="min" :max="max" :value="value" :step="step" :price="price" @input="updateVal()">
  <div class="quantity-nav">
    <a class="quantity-button quantity-up" @click="quantityUp" v-show="onUp">+</a>
    <a class="quantity-button quantity-down" @click="quantityDown" v-show="onDown">-</a>
  </div>
  <span style="margin-left:10px; line-height: 40px;">
    ￥{{quantity * price}}
  </span>
</div>
</template>
<script>
  export default {
    props: {
      min: {
        type: Number,
        default: 1
      },
      max: {
        type: Number,
        default: 255
      },
      step: {
        type: Number,
        default: 1
      },
      value: {
        type: Number,
        default: 1
      },
      price: {
        type: Number,
        default: 75
      }
    },
    data () {
      return {
        quantity: 1,
        onUp: true,
        onDown: true
      }
    },
    methods: {
      quantityUp (event) {
        event.target.parentNode.parentNode.firstChild.stepUp()
        this.updateVal()
      },
      quantityDown (event) {
        event.target.parentNode.parentNode.firstChild.stepDown()
        this.updateVal()
      },
      updateVal () {
        this.$emit('input', Number(this.$refs.input.value))
        this.quantity = Number(this.$refs.input.value)
      }
    }
  }
</script>
<style>
  .quantity {
    position: relative;
  }

  input[type=number]::-webkit-inner-spin-button,
  input[type=number]::-webkit-outer-spin-button
  {
    -webkit-appearance: none;
    margin: 0;
  }

  input[type=number]
  {
    -moz-appearance: textfield;
  }

  .quantity input {
    width: 60px;
    height: 42px;
    float: left;
    display: block;
    padding: 0;
    margin: 0;
    padding-left: 20px;
    border: 1px solid #eee;
  }

  .quantity input:focus {
    outline: 0;
  }

  .quantity-nav {
    float: left;
    position: relative;
    height: 42px;
  }

  .quantity-button {
    position: relative;
    cursor: pointer;
    border-left: 1px solid #eee;
    width: 20px;
    text-align: center;
    color: #333;
    font-size: 13px;
    font-family: "Trebuchet MS", Helvetica, sans-serif !important;
    line-height: 1.7;
    -webkit-transform: translateX(-100%);
    transform: translateX(-100%);
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
  }

  .quantity-button.quantity-up {
    position: absolute;
    height: 50%;
    top: 0;
    border-bottom: 1px solid #eee;
  }

  .quantity-button.quantity-down {
    position: absolute;
    bottom: -1px;
    height: 50%;
  }
</style>


